.App {
  text-align: center;
  background-color: #1b1826;;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}


.drum-machine {
    color:aliceblue;
    border: 2px solid black;
    padding: 20px 5px; 
    height :500px;
    width: 500px;
    border-radius: 5px;
    background: rgb(42, 42, 42);
}

.pad-bank { 
  margin: 2%  auto;
  width: 80%;
  height: 60%;
  border-radius: 5px;
  padding: 5px;
  border: 2px solid rgb(156, 156, 156);
}


.pad { 
  border-radius: 5px;
  padding: 21px;
  margin: 2px;
  background-color: grey;
  cursor: pointer;
  font-size: 15px;
  box-shadow: 2px 2px 2px black;
  user-select: none ;
}
.pad:hover {
  background-color: rgba(128, 128, 128, 0.395);

}

.pad-container { 
  display: grid;
  margin: 2px;
  grid-template-columns: auto auto auto;
}

.display {
  margin: 0 auto;
  width: 40%;
  border: #1b1826 1px solid;
  border-radius: 1px;
  background-color: rgb(67, 66, 66);
  font-family: digital-7;
}

.logo {
  text-align: right;
  margin: 5px;
  margin-right: 20px;
  font-family: viking;
}

.controls {
  margin: 2%  auto;
  width: 80%;
  height: 14%;
  border: 1px solid #f5f5f5;
  border-radius: 4px;
  display: grid;
  padding: 10px;
  grid-template-columns: auto auto ;


}

.control-group {
  font-size: 10px;
  margin:  0 auto;;
  width: 80%;
}

.control-title { 
  font-weight: 700;
  border: #d3d3d3 1px solid;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  background: #d3d3d3;
  outline: none;
  margin: 5px;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 10px;
  background: #eaeaea;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 10px;
  height: 15px;
  border-radius: 0px;
  background: #eaeaea;
  cursor: pointer;
}